.h-img {
  width: 60px;
  height: 60px;
  background-size: cover;
}
.h-weather-details {
  width: clamp(130px, 7.5vw, 150px);
  height: clamp(150px, 10vw, 180px);
}

.hourly-weather-widget-animaiton {
  animation-name: show-up;
  animation-duration: 500ms;
}

@keyframes show-up {
  0% {
    transform: rotateY(0deg);
    opacity: 0;
  }
  50% {
    transform: rotateY(90deg);
    opacity: 0.5;
  }

  100% {
    transform: rotateY(0deg);
    opacity: 1;
  }
}
